import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';

@Injectable()
export class AuthService {

  authToken: any;
  user: any;

  constructor(private http: Http) { 

  }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/register', user, {headers: headers})
      .map(res => res.json());
  }

  authenticateUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/authenticate', user, {headers: headers})
      .map(res => res.json());
  }

  getProfile(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get('users/profile', {headers: headers})
      .map(res => res.json());
  }

  getQuote(){
    //console.log("test");
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    //console.log("what is this", this.http.get('quote/quote', {headers: headers})
    //.map(res => res.json()));
    return this.http.get('quote/quote', {headers: headers})
      .map(res => res.json());
  }

  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn(){
    return tokenNotExpired('id_token');
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

  setQuote(newQuote){
    //console.log("setquote auth.service ", quote);
    //let body = "{ " + "\"quote\"" + " = " + "\"" + quote + "\" }";
    //console.log(body);
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.put('quote/changequote', JSON.stringify({quote: newQuote}), {headers: headers})
      .map(res => res.json());
  }
}



